* {
  box-sizing: border-box;
}

body {
  margin: 0;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
}

.content {
  display: flex;
  align-items: center;
  flex-direction: column;
}

#snap-btn {
  font-size: 20px;
  padding: 20px 40px 20px 80px;
  margin-top: 30px;
  border-radius: 10px;
  background: url("../assets/gauntlet-snap.gif") white 15px no-repeat;
  background-size: 50px;
}

.dust {
  position: absolute;
}

.canvas_container {
  position: absolute;
  top: 0px;
}
